import './card.css'
import { useNavigate } from 'react-router-dom'
import { useDispatch, useSelector } from 'react-redux'
import { add2cart } from '../../redux/slices/cart'

const Card = (props) => {
    const navigate = useNavigate()
    const dispatch = useDispatch()
    const RegLoginInfo = useSelector(reducers => reducers.RegLoginReducer)
    const { _id, image, name, price } = props.details

    return (
        <div className="card" >
            <div className='wrapper'>
                <div className="cardDetails thumbDiv"><img alt={image} src={image}></img></div>
                <div className="cardDetails">{name}</div>
                <div className="cardDetails"> Rs.{price}</div>
            </div>

            <div className="cardDetails">
                <button onClick={
                    () => {
                        navigate(`/productpage/${_id}`)
                    }
                }>More details </button>
            </div>
            <div className="cardDetails">
                {props.details.quantity > 0 && <button onClick={() => {

                    RegLoginInfo.userAuth.token ?
                        dispatch(add2cart({ userId: RegLoginInfo.loggedinId, product: props.details })) : alert('Sign in to add items in cart')

                }}>Add to cart</button>}
            </div>


        </div>
    )
}

export default Card