import './checkoutpage.css'
import { useSelector, useDispatch } from 'react-redux'
import { useNavigate } from 'react-router-dom'
import { useEffect, useState } from 'react'
import axios from 'axios'
import { setUserObj } from '../../redux/slices/RegLogin'
import DropIn from "braintree-web-drop-in-react";
import { setCartsArr } from '../../redux/slices/cart'


const CheckoutPage = () => {
    const navigate = useNavigate()
    const dispatch = useDispatch()
    const cartInfo = useSelector(reducers => reducers.CartReducer)
    const RegLoginInfo = useSelector(reducers => reducers.RegLoginReducer)
    const userCart = cartInfo.cartsArr.find(x => x.owner === RegLoginInfo.loggedinId)
    const cartLength = userCart ? userCart.cartItems.length : 0

    const [clientToken, setClientToken] = useState(null)
    const [instance, setInstance] = useState(null)

    const [currentAddress, setCurrentAddress] = useState('')
    const [updatedAddress, setUpdatedAddress] = useState('')
    const [updateScreen, setUpdateScreen] = useState(false)
    const totalItemsCalc = () => {
        const { quantityArr } = cartInfo.cartsArr.find(x => x.owner === RegLoginInfo.loggedinId)
        return quantityArr.reduce((acc, curr) => {
            acc = +acc + +curr.quantity
            return acc
        }, 0)
    }
    const calcQuantity = (_id) => {
        const { quantityArr } = cartInfo.cartsArr.find(x => x.owner === RegLoginInfo.loggedinId)
        const { quantity } = quantityArr.find(x => x._id === _id)
        return quantity
    }

    const calculateBill = () => {
        const { cartItems, quantityArr } = cartInfo.cartsArr.find(x => x.owner === RegLoginInfo.loggedinId)
        return cartItems.reduce((acc, curr) => {
            const { quantity } = quantityArr.find(x => x._id === curr._id)
            acc = +acc + (+curr.price * quantity)
            return acc
        }, 0)
    }

    const addressUpdater = async () => {
        let oldData = RegLoginInfo.userObj
        let oldAddress = oldData.address
        let sendObj = { ...oldAddress, address: updatedAddress }
        try {
            const response = await axios.post('/updateuser', { updateData: sendObj, email: oldData.email })
            dispatch(setUserObj({ ...response.data.updatedUser }))
            setUpdateScreen(false)
        } catch (error) {
            alert(error.message)
        }
    }

    useEffect(() => {
        if (RegLoginInfo.userObj !== null) {
            setCurrentAddress(RegLoginInfo.userObj.address)
        }
    }, [RegLoginInfo.userObj])

    const paymentHandler = async () => {
        const billAmount = calculateBill()
        const buyerCart = cartInfo.cartsArr.find(x => x.owner === RegLoginInfo.loggedinId)
        try {
            const { nonce } = await instance.requestPaymentMethod()
            const response = await axios.post('/paymentgateway/transaction', {
                buyerCart, nonceFromClient: nonce, billAmount
            })
            alert('Payment Successful')
            dispatch(setCartsArr(null))
            RegLoginInfo.userAuth.type!==null && RegLoginInfo.userAuth.userType === 0 ? navigate('/dashboard/user/orders') : navigate('/dashboard/admin/personalorders')

        } catch (error) {
            alert(error.message)
        }


    }

    const getToken = async () => {
        const response = await axios.get(`/paymentgateway/gettoken/${RegLoginInfo.loggedinId}`)
        setClientToken(response.data)
    }

    useEffect(() => {
        if (RegLoginInfo.verified.login === true && RegLoginInfo.loggedinId !== null) {
            getToken(RegLoginInfo.loggedinId)
        }

    }, [RegLoginInfo.verified.login, RegLoginInfo.loggedinId])

    return (
        <div className="checkoutPage">
            {cartLength === 0 ? <h2>No products is present in the cart for checkout.</h2> :
                <>
                    <div className="cartSummary">
                        <h2>Your Cart's Summary</h2>
                        <div className='itemsDisplay'>
                            <div className='pItem2'>
                                <div >Total Products : </div>
                                <div >{cartLength !== 0 && cartLength}</div>
                            </div>
                            <div className='pItem2'>
                                <div >Total Items : </div>
                                <div >{cartLength !== 0 && totalItemsCalc()}</div>
                            </div>
                            <div className='pItem'>
                                <div className='pDesc'>Item Description</div>
                                <div className='pQuant'>Quantity</div>
                                <div className='pPrice'>Price</div>
                                <div className='pAmount'>Amount</div>
                            </div>

                            <div className='itDis'>
                                {cartLength !== 0 && userCart.cartItems.map(item => <div className='pItem '>
                                    <div className='pDesc'><div><img alt={item.image} style={{ width: '70px', maxHeight: '70px' }} src={item.image}></img></div><div>{item.name}</div></div>
                                    <div className='pQuant'>{calcQuantity(item._id)}</div>
                                    <div className='pPrice'>Rs.{item.price}</div>
                                    <div className='pAmount'>Rs.{calcQuantity(item._id) * item.price}</div>
                                </div>
                                )}

                            </div>


                            <div className='pItem3'>
                                <div >Total Amount : </div>
                                <div >Rs.{cartLength !== 0 && calculateBill()}</div>
                            </div>


                        </div>


                        <div className='billDisplay'>

                            <div>Shipping Charges : Nil</div>
                            <div>Bill Amount :  Rs.{cartLength !== 0 && calculateBill()}</div>
                        </div>

                    </div>
                    <div className="checkoutDiv">
                        {!updateScreen ? (
                            <div className="shipAddress">
                                <div className="shipHeader"><h2>Shipping Address</h2></div>
                                <div className="shipDisplay">{currentAddress}</div>
                                <div className="shipFooter"><button onClick={() => setUpdateScreen(true)}>Edit Shipping Address</button></div>
                            </div>
                        ) : (
                            <div className="shipAddress">
                                <div className="shipHeader"><h2>Shipping Address</h2></div>
                                <div className="shipDisplay"><textarea style={{ width: '90%', height: '90%' }} value={updatedAddress} onChange={(e) => setUpdatedAddress(String(e.target.value))}></textarea></div>
                                <div className="shipFooter"><button onClick={() => setUpdateScreen(false)}>Cancel Edit</button><button onClick={addressUpdater}>Update Shipping Address</button></div>
                            </div>
                        )}

                        <div className='paymentInterfaceDiv'>
                            {clientToken &&
                                <>
                                    <DropIn
                                        options={{ authorization: clientToken, paypal: { flow: 'vault' } }}
                                        onInstance={(instance) => setInstance(instance)}
                                    />
                                    <button className='paybtn' onClick={paymentHandler}>Proceed to Pay</button>
                                </>
                            }

                        </div>

                    </div>

                </>

            }

        </div>
    )
}

export default CheckoutPage