import axios from 'axios'
import { useEffect, useState } from 'react'
import { useSelector } from 'react-redux'
import moment from 'moment'
import './order.css'


const OrderPage = () => {

    const RegLoginInfo = useSelector(reducers => reducers.RegLoginReducer)
    const [order, setOrder] = useState(null)
    const [msg, setMsg] = useState('')
    const fetchOrder = async (id) => {
        try {
            const response = await axios.get(`/orders/${id}`)
            setOrder(() => response.data.order.length > 0 && response.data.order)
        } catch (error) {
            setMsg(() => error.response.data.message)
        }

    }
    useEffect(() => {
        fetchOrder(RegLoginInfo.loggedinId)
    }, [])
    return (
        <>
            <h2>Your Orders History</h2>

            {!order ? 'No order has been placed by you..' : order.map(o => {
                return (
                    <div style={{ margin: '5vmin', border: '5px solid black' }}>

                        <div style={{ display: 'flex', justifyContent: 'space-evenly', flexWrap: "wrap" }}>
                            <div style={{ marginRight: '4rem' }}>Ordered {moment(o?.createdAt).fromNow()}</div>
                            <div style={{ marginRight: '4rem' }}>Order ID : {o._id}</div>
                            <div style={{ marginRight: '4rem' }}>Bill Amount : Rs.{o.payment?.transaction?.amount}</div>
                        </div>

                        <div style={{ border: '1px solid black',margin:"1rem" }}>
                            {o.purchasedItems.products.map((x, i) => {
                                return (
                                    <>
                                    <div>Order Item : {i + 1}</div>
                                    <div className='productDiv'>
                                        
                                        <div className="partDivs">
                                            <div className="content"><b>Shipment Status</b></div>
                                            <div className="content"><b>Payment Status</b></div>
                                            <div className="content"><b>Image</b></div>
                                            <div className="content">{o.status}</div>
                                            <div className="content">{o.payment.success === true ? 'Successful' : 'Failed'}</div>
                                            <div className="content"><img alt={x.image} style={{ width: '100px', maxHeight: '100px' }} src={x.image}></img></div>
                                        </div>
                                        <div className="partDivs">
                                            <div className="content"><b>Name</b></div>
                                            <div className="content"><b>Price</b></div>
                                            <div className="content"><b>Quantity</b></div>
                                            <div className="content">{x.name}</div>
                                            <div className="content">{x.price}</div>
                                            <div className="content">{(o.purchasedItems.productsQuantity.find(y => y._id === x._id)).quantity}</div>
                                        </div>

                                    </div>
                                    </>
                                    

                                )

                            })}


                        </div>

                    </div>
                )

            })}
            

        </>
    )
}

export default OrderPage