import axios from "axios"
import { Outlet } from "react-router-dom"
import { useEffect,useState } from "react"
import { useSelector, useDispatch } from "react-redux"
import { setVerified, setUserObj } from "../redux/slices/RegLogin"
const ProtectedRoute = () => {
  const dispatch = useDispatch()
  const RegLoginInfo = useSelector(reducers => reducers.RegLoginReducer)
  const [isLoginVerified,setisLoginVerified] = useState(null)

  useEffect(() => {
    axios.defaults.headers.common['Authorization'] !== undefined && axios.get('/verifylogin').then(() => {
      dispatch(setVerified({ login: true }))
      setisLoginVerified(true)

      axios.get('/verifyadmin').then((resp) => {

        if (resp.status === 200 && resp.data.verifyadmin === true) {

          dispatch(setVerified({ admin: true }))
        } else if (resp.status === 200 && resp.data.verifyadmin === false) {
          dispatch(setVerified({ admin: false }))
        }

        if (RegLoginInfo.userObj === null) {
          axios.post('/getuserdetail', { token: RegLoginInfo?.userAuth?.token }).then(response => dispatch(setUserObj(response.data.userObj))).catch(err => console.log(err))
        }


      })

    }).catch((err) => {
      dispatch(setVerified({ login: false }))
      setisLoginVerified(false)
    })
  }, [])

  return (
    <>
      {/* {RegLoginInfo.verified.login === null ? 'Wait please! Loading...' : RegLoginInfo.verified.login === false ? 'Sign in is required' : <Outlet />}
      
      {RegLoginInfo.verified.login === null ? localStorage.setItem("verifiedLogin","null") : RegLoginInfo.verified.login === false ? localStorage.setItem("verifiedLogin","false") : localStorage.setItem("verifiedLogin","true")} */}
      {isLoginVerified === null ? 'Wait please! Loading...' : isLoginVerified === false ? 'Sign in is required' : <Outlet />}
      
      {isLoginVerified === null ? localStorage.setItem("verifiedLogin","null") : isLoginVerified === false ? localStorage.setItem("verifiedLogin","false") : localStorage.setItem("verifiedLogin","true")}
    </>
  )
}

export default ProtectedRoute