import axios from "axios"
import { useEffect } from "react"
import { useSelector, useDispatch } from "react-redux"
import { setProductsArr } from '../redux/slices/product'
import ProductCard from './productcard'

const ProductsList = () => {
    const dispatch = useDispatch()
    const ProductsInfo = useSelector(reducers => reducers.ProductReducer)

    useEffect(() => {
        axios.get('/products/all').then(res => {
            dispatch(setProductsArr(res.data.products))
        }).catch(err => alert(err.message))
    }, [])

    return (
        <div style={{display:'flex',flexWrap:'wrap',justifyContent:'space-evenly'}}>
            {ProductsInfo.productsArr.map(product => <ProductCard details={product} />)}
        </div>
    )
}

export default ProductsList