import axios from "axios"
import { Outlet } from "react-router-dom"
import { useEffect,useState } from "react"
import { useSelector, useDispatch } from "react-redux"
import { setVerified, setUserObj, setLoggedInId, setUserAuth } from "../redux/slices/RegLogin"

const CartProtectedRoute = () => {
    const dispatch = useDispatch()
    const RegLoginInfo = useSelector(reducers => reducers.RegLoginReducer)
    const [isLoginVerified,setisLoginVerified] = useState(null)

    useEffect(() => {
        axios.defaults.headers.common['Authorization'] !== undefined && axios.get('/verifylogin').then(() => {
            dispatch(setVerified({ login: true }))
            setisLoginVerified(true)

            if (RegLoginInfo.userObj === null) {
                
                axios.post('/getuserdetail', { token: RegLoginInfo?.userAuth?.token }).then(response => dispatch(setUserObj(response.data.userObj))).catch(err => console.log(err))
            }

        }).catch((err) => {
            console.log(err.response)
            dispatch(setLoggedInId(null))
            dispatch(setUserAuth({ token: null, userType: null }))
            dispatch(setVerified({ login: null }))
            setisLoginVerified(false)
            dispatch(setVerified({ admin: null }))
            localStorage.removeItem('userAuth')
        })
    }, [])

    return (
        <>
            {/* {RegLoginInfo.verified.login === null ? 'Wait please! Loading...' : RegLoginInfo.verified.login === false ? 'Sign in is required' : <Outlet />}
            {RegLoginInfo.verified.login === null ? localStorage.setItem("cartverifiedLogin","null") : RegLoginInfo.verified.login === false ? localStorage.setItem("cartverifiedLogin","false") : localStorage.setItem("cartverifiedLogin","true")} */}
            {isLoginVerified === null ? 'Wait please! Loading...' : isLoginVerified === false ? 'Sign in is required' : <Outlet />}
            {isLoginVerified === null ? localStorage.setItem("cartverifiedLogin","null") : isLoginVerified === false ? localStorage.setItem("cartverifiedLogin","false") : localStorage.setItem("cartverifiedLogin","true")}
        </>
    )
}

export default CartProtectedRoute