import { createSlice } from '@reduxjs/toolkit'

const CartSliceMaker = createSlice({
    name: 'CartSlice',
    initialState: {
        cartsArr: []
    },
    reducers: {
        add2cart: (state, action) => {
            const { userId, product } = action.payload
            if (!state.cartsArr.some(x => x.owner === userId)) {
                state.cartsArr.push({ owner: userId, cartItems: [product], quantityArr: [{ _id: product._id, quantity: 1 }] })
            } else {
                for (let key in state.cartsArr) {
                    if (state.cartsArr[key].owner === userId) {
                        if (!state.cartsArr[key].cartItems.some(x => x._id === product._id)) {
                            state.cartsArr[key].cartItems.push(product)
                            state.cartsArr[key].quantityArr.push({ _id: product._id, quantity: 1 })
                            break
                        } else {
                            for (let qKey in state.cartsArr[key].quantityArr) {
                                if (state.cartsArr[key].quantityArr[qKey]._id === product._id) {
                                    state.cartsArr[key].quantityArr[qKey].quantity += 1
                                    break;
                                }
                            }
                        }
                    }
                }
            }
        },
        removeFromCart: (state, action) => {
            const { userId, productId } = action.payload
            for (let key in state.cartsArr) {
                if (state.cartsArr[key].owner === userId) {
                    state.cartsArr[key].cartItems = [...state.cartsArr[key].cartItems].filter(x => x._id !== productId)
                    state.cartsArr[key].quantityArr = state.cartsArr[key].quantityArr.filter(x => x._id !== productId)
                    break
                }
            }
        },
        quantityChanger: (state, action) => {
            const { userId, productId, quantity } = action.payload
            for (let key in state.cartsArr) {
                if (state.cartsArr[key].owner === userId) {
                    for (let qKey in state.cartsArr[key].quantityArr) {
                        if (state.cartsArr[key].quantityArr[qKey]._id === productId) {
                            state.cartsArr[key].quantityArr[qKey].quantity = quantity
                            break
                        }
                    }
                }

            }
        },
        cartEmptier: (state, action) => {
            const userId = action.payload
            for (let key in state.cartsArr) {
                if (state.cartsArr[key].owner === userId) {
                    state.cartsArr[key].cartItems = []
                    state.cartsArr[key].quantityArr = []
                }
                break
            }

        },
        setCartsArr: (state, action) => {
            state.cartsArr = action.payload === null ? [] : [action.payload]
        }
    }
})

export const { add2cart, removeFromCart, quantityChanger, cartEmptier, setCartsArr } = CartSliceMaker.actions
export default CartSliceMaker.reducer