import axios from 'axios'
import './productsCard.css'
import { removeProduct, setProduct2edit } from '../redux/slices/product'
import { useDispatch } from 'react-redux'
import { useNavigate } from 'react-router-dom'
const ProductCard = (props) => {
    const navigate = useNavigate()
    const dispatch = useDispatch()
    const { name, price, description, category, quantity, image, _id } = props.details
    const productRemover = async (id) => {
        try {
            const resp = await axios.delete(`/products/${id}`)
            dispatch(removeProduct(id))
            alert(resp.data.message)
        } catch (error) {
            alert(error.message)
        }
    }

    return (
        <div className='productCard' >
            <div className='productDetail'><div><img alt={image} src={image}></img></div></div>
            <div className='productDetail namepd'>Name:<div><h4>{name}</h4></div></div>
            <div className='productDetail descpd'>Description:<div>{description}</div></div>
            <div className='combinedpd'>
                <div className='productDetail'>Price: Rs.{price}</div>
                <div className='productDetail'>Category: {category}</div>
                <div className='productDetail'>Quantity: {quantity}</div>
            </div>

            <div className='btnspd'>
                <div className='productDetail'><button onClick={() => {
                    dispatch(setProduct2edit({ name, price, description, category, quantity, image, _id }))
                    navigate('/dashboard/admin/editproduct')
                }}>Edit Product</button></div>
                <div className='productDetail'><button onClick={() => {
                    productRemover(_id)
                }}>Delete Product</button></div>
            </div>
        </div>
    )
}

export default ProductCard