import { NavLink } from "react-router-dom"
import './menu.css'
const Adminmenu = () => {
    return (
        <div className="menu">
            <div><NavLink className='navlinks' to='/dashboard/admin/orders'>Buyer's Orders</NavLink></div>
            <div><NavLink className='navlinks' to='/dashboard/admin/personalorders'>Orders</NavLink></div>
            <div><NavLink className='navlinks' to='/dashboard/admin/products'>Products</NavLink></div>
            <div><NavLink className='navlinks' to='/dashboard/admin/addproduct'>Add Products</NavLink></div>
            <div><NavLink className='navlinks' to='/dashboard/editprofile'>Edit Profile</NavLink></div>
        </div>
    )
}

export default Adminmenu