import { NavLink } from "react-router-dom"
import { useSelector, useDispatch } from "react-redux"
import { setUserAuth, setVerified } from '../../redux/slices/RegLogin'
import './Navbar.css'
import decode from 'jwt-decode'
import axios from "axios"
const token = JSON.parse(localStorage.getItem('userAuth'))?.token
const { _id: decodedId } = token !== undefined && decode(token)


const Navbar = () => {
  const dispatch = useDispatch()
  const RegLoginInfo = useSelector(reducers => reducers.RegLoginReducer)
  const cartInfo = useSelector(reducers => reducers.CartReducer)

  const userCart = cartInfo.cartsArr.find(x => x.owner === RegLoginInfo.loggedinId || decodedId)

  const cartLength = userCart ? userCart.cartItems.length : 0

  function handleLogout() {
    dispatch(setUserAuth({ token: null, userType: null }))
    dispatch(setVerified({ login: null }))
    dispatch(setVerified({ admin: null }))
    localStorage.removeItem('userAuth')
    const cartsList = cartInfo.cartsArr.filter(x => x.owner !== null)
    cartsList.length>0 && axios.post('/carts/add', cartsList).then(res => console.log('Cart saved successfully')).catch(err => console.log(err.message))
  }
  return (
    <div className="navbar">
      <div className="logo">
        <NavLink className='navlinks' to='/'>E-Commerce by Nitesh</NavLink>
      </div>

      <div className="links">
        {RegLoginInfo.userAuth?.userType !== null ? (
          <>
            <NavLink className='navlinks' to={RegLoginInfo.userAuth?.userType === 1 ? '/dashboard/admin' : '/dashboard/user'} >Dashboard</NavLink>
            <NavLink className='navlinks' onClick={handleLogout} to='/login'>Logout</NavLink>
            <NavLink className='navlinks' to='/cart'>Cart({cartLength})</NavLink>
          </>

        ) : (
          <>
            <NavLink className='navlinks' to='/register'>Sign Up</NavLink>
            <NavLink className='navlinks' onClick={() => {
              dispatch(setVerified({ login: null }))
            }} to='/login'>Sign In</NavLink>
            {RegLoginInfo.userAuth.token !== null ? <NavLink className='navlinks' to='/cart'>Cart({cartLength})</NavLink> : <NavLink className='navlinks' onClick={() => alert('Sign in is required to access cart')}>Cart</NavLink>}
          </>
        )}

      </div>
    </div>
  )
}

export default Navbar