import { useNavigate } from 'react-router-dom'
import './updatepass.css'
import axios from "axios";
import { setUpdatePassData } from '../../redux/slices/RegLogin'
import { useDispatch, useSelector } from 'react-redux';

const LoginForm = () => {
    const dispatch = useDispatch()
    const RegLoginInfo = useSelector(reducers => reducers.RegLoginReducer)
    const updatePassData = RegLoginInfo.updatePassData

    const navigate = useNavigate()

    function handleChange(e) {
        const { id, value } = e.target
        dispatch(setUpdatePassData({ [id]: value }))
    }

    async function handleLogin(e) {
        e.preventDefault()
        const valuesArr = Object.values(updatePassData)
        let formFields = []
        for (let key in valuesArr) {
            (valuesArr[key] === '') && formFields.push(Object.keys(updatePassData)[key])
        }
        if (formFields.length === 0) {
            try {
                const response = await axios.post('/forgotpassword', updatePassData)
                alert(response.data.message)
                String(response.status) === '200' ? navigate('/login') : alert(response.data.message)
            } catch (error) {//this catch block also catches client side data errors
                alert(error.response.data.message)
            }

            dispatch(setUpdatePassData({ email: '', newpassword: '', secretanswer: '' }))

        } else if (formFields.length < valuesArr.length) {
            alert(`${formFields.join(',')} are empty`)
        } else {
            alert('All field must be filled to Login')
        }
    }
    return (
        <div className='updatePassPage'>
            <div className='formContainer' >
                <form className='updatePassForm' onSubmit={e => handleLogin(e)}>

                    <div className='formsComponent'>
                        <label htmlFor="email">Email:</label>
                        <input type="email" id="email" value={updatePassData.email} onChange={(e) => handleChange(e)} required />
                    </div>

                    <div className='formsComponent'>
                        <label htmlFor="secretanswer">Secret Answer:</label>
                        <input type="text" id="secretanswer" value={updatePassData.secretanswer} onChange={(e) => handleChange(e)} required />
                    </div>
                    <div className='formsComponent'>
                        <label htmlFor="newpassword">Create New Password:</label>
                        <input type="password" id="newpassword" value={updatePassData.newpassword} onChange={(e) => handleChange(e)} required />
                    </div>

                    <div className='formsComponent'>
                        <input type="submit" value="Update Password" />
                    </div>

                </form>
            </div>

        </div>
    )
}

export default LoginForm