import './edit.css'
import { setProduct2edit } from '../redux/slices/product'
import { useSelector, useDispatch } from 'react-redux'
import { useNavigate } from 'react-router-dom'
import { useState, useEffect } from 'react'
import axios from 'axios'
const EditProduct = () => {
    const dispatch = useDispatch()
    const productInfo = useSelector(reducers => reducers.ProductReducer)

    const [product, setProduct] = useState({ name: '', price: '', quantity: '', description: '', category: '', image: null })

    const navigate = useNavigate()

    useEffect(() => {
        if (productInfo.product2edit) {
            setProduct({ ...productInfo.product2edit })
        }
    }, [productInfo.product2edit])

    const convert2base64 = (imgFile) => {
        return new Promise((resolve, reject) => {
            const fileReader = new FileReader()
            fileReader.readAsDataURL(imgFile)
            fileReader.onload = () => resolve(fileReader.result)
            fileReader.onerror = (err) => reject(err)
        })
    }
    const handleChange = async (e) => {
        if (e.target.files) {
            const imgfile = e.target.files[0]
            const fileSize = e.target.files[0].size;
            const sizeLimit = 5 * 1024 * 1024;//5MB    
            if (fileSize <= sizeLimit) {
                const imgInBase64 = await convert2base64(imgfile)
                const { id } = e.target
                if (id === 'image') {
                    setProduct(() => ({ ...product, [id]: imgInBase64 }))
                }
            } else {
                alert('Size limit exceeded');
                e.target.value = ''; // Clear the selected file
            }
        }else{
            const { id,value } = e.target
            setProduct(() => ({ ...product, [id]: value }))
        }
    };

   
    const handleProductEdit = async (e) => {
        e.preventDefault()
        const valuesArr = Object.values(product)
        let formFields = []
        for (let key in valuesArr) {
            (valuesArr[key] === '' || valuesArr[key] === null) && formFields.push(Object.keys(product)[key])
        }
        if (formFields.length === 0) {
            let formData = new FormData()
            let entries = Object.entries(product)
            for (let key in entries) {
                formData.append(`${entries[key][0]}`, entries[key][1])
            }

            await axios.patch(`/products/editproduct/${productInfo?.product2edit._id}`, formData, {
                headers: {
                    'Content-Type': 'application/json'
                }
            }).then(response => {
                alert(response.data.message)
                dispatch(setProduct2edit(null))
                navigate('/dashboard/admin')
            }).catch(err => {
                alert('Error :', err.message)
                setProduct({ ...productInfo.product2edit })
            })

        } else if (formFields.length < valuesArr.length) {
            alert(`${formFields.join(',')} are empty`)
        } else {
            alert('All field must be filled to Register')
        }

    }
    return (<>{productInfo.product2edit !== null &&
        <div className='editcontainer'>
            <form className='signupForm' onSubmit={e => handleProductEdit(e)} encType='multipart/form-data'>

                <div className='formsComponent'>
                    <label htmlFor="name">Name:</label>
                    <input type="text" id="name" value={product.name} onChange={(e) => handleChange(e)}  />
                </div>

                <div className='formsComponent'>
                    <label htmlFor="price">Price:</label>
                    <input type="number" id="price" value={product.price} onChange={(e) => handleChange(e)}  />
                </div>

                <div className='formsComponent'>
                    <label htmlFor="category">Category:</label>
                    <input type="text" id="category" value={product.category} onChange={(e) => handleChange(e)}  />
                </div>

                <div className='formsComponent'>
                    <label htmlFor="description">Description:</label>
                    <input type="text" id="description" value={product.description} onChange={(e) => handleChange(e)}  />
                </div>

                <div className='formsComponent'>
                    <label htmlFor="quantity">Quantity:</label>
                    <input type="number" id="quantity" value={product.quantity} onChange={(e) => handleChange(e)}  />
                </div>

                <div className='formsComponent'>
                    <label htmlFor="image">Upload Image:</label>
                    <input type='file' id="image" onChange={(e) => handleChange(e)}  />
                </div>


                <div className='formsComponent'>
                    <input type="submit" value="Update Product" />
                </div>

            </form>
        </div>
    }</>

    )
}

export default EditProduct