import { useNavigate } from 'react-router-dom'
import './signUp.css'
import axios from "axios";
import { setRegisterData } from '../../redux/slices/RegLogin'
import { useDispatch, useSelector } from 'react-redux';

const SignUpForm = () => {
  const dispatch = useDispatch()
  const RegLoginInfo = useSelector(reducers => reducers.RegLoginReducer)
  const registerData = RegLoginInfo.register

  const navigate = useNavigate()
  function handleChange(e) {
    const { id, value } = e.target
    dispatch(setRegisterData({ [id]: value }))
  }

  function handleRegister(e) {
    e.preventDefault()
    const valuesArr = Object.values(registerData)
    let formFields = []
    for (let key in valuesArr) {
      (valuesArr[key] === '') && formFields.push(Object.keys(registerData)[key])
    }
    if (formFields.length === 0) {
      axios.post('/register', registerData).then(response => {

        alert(response.data.message);
        String(response.status) === '200' && navigate('/login')
      }).catch(err => alert('Error :', err))
 
      dispatch(setRegisterData({ username: '', email: '', mobile: '', address: '', password: '', secretanswer: '' }))

    } else if (formFields.length < valuesArr.length) {
      alert(`${formFields.join(',')} are empty`)
    } else {
      alert('All field must be filled to Register')
    }
  }
  return (
    <div className='registerPage'>
      <div className='formContainer' >
        <form className='signupForm' onSubmit={e => handleRegister(e)}>

          <div className='formsComponent'>
            <label htmlFor="username">Name:</label>
            <input type="text" id="username" value={registerData.username} onChange={(e) => handleChange(e)} required />
          </div>

          <div className='formsComponent'>
            <label htmlFor="email">Email:</label>
            <input type="email" id="email" value={registerData.email} onChange={(e) => handleChange(e)} required />
          </div>

          <div className='formsComponent'>
            <label htmlFor="mobile">Mobile:</label>
            <input type="number" id="mobile" value={registerData.mobile} onChange={(e) => handleChange(e)} required />
          </div>

          <div className='formsComponent'>
            <label htmlFor="password">Create Password:</label>
            <input type="password" id="password" value={registerData.password} onChange={(e) => handleChange(e)} required />
          </div>

          <div className='formsComponent'>
            <label htmlFor="address">Address:</label>
            <input type="text" id="address" value={registerData.address} onChange={(e) => handleChange(e)} required />
          </div>

          <div className='formsComponent'>
            <label htmlFor="secretanswer">Secret Answer:</label>
            <input type="text" id="secretanswer" value={registerData.secretanswer} onChange={(e) => handleChange(e)} required />
          </div>


          <div className='formsComponent'>
            <input type="submit" value="Register" />
          </div>

        </form>
        <div className='go2login'>
          <button onClick={() => navigate('/login')}>Already Registered? Click here to Login</button>
        </div>
      </div>

    </div>
  )
}

export default SignUpForm