import './cart.css'
import { useDispatch, useSelector } from "react-redux"
import CartItem from './cartitem.jsx'
import { cartEmptier } from '../../redux/slices/cart'
import {useNavigate} from 'react-router-dom'

const Cart = () => {
    const navigate = useNavigate()
    const dispatch = useDispatch()
    const cartInfo = useSelector(reducers => reducers.CartReducer)
    const RegLoginInfo = useSelector(reducers => reducers.RegLoginReducer)
    const userCart = cartInfo.cartsArr.find(x => x.owner === RegLoginInfo.loggedinId)
    const cartLength = userCart?userCart.cartItems.length:0

    const calculateBill = () => {
        const { cartItems, quantityArr } = cartInfo.cartsArr.find(x=>x.owner === RegLoginInfo.loggedinId)
        return cartItems.reduce((acc, curr) => {
            const { quantity } = quantityArr.find(x => x._id === curr._id)
            acc = +acc + (+curr.price * quantity)
            return acc
        }, 0)
    }

    const totalItemsCalc = () => {
        const { quantityArr } = cartInfo.cartsArr.find(x=>x.owner === RegLoginInfo.loggedinId)
        return quantityArr.reduce((acc, curr) => {
            acc = +acc + +curr.quantity
            return acc
        }, 0)
    }

    const emptyCart = () => {
        dispatch(cartEmptier(RegLoginInfo.loggedinId))
    }
    return (
        <div className='cartPage'>
            <div className="cart">

                <div className="itemsContainer">
                    {cartLength === 0 ? 'Cart is empty' : userCart.cartItems.map(item => <CartItem key={item._id} product={item} />)}
                </div>

                {cartLength !== 0 && <div className="subtotal">

                    <div className="totalComponents"><button onClick={emptyCart}>Remove All</button></div>

                    <div className="totalComponents">Total products : {cartLength}</div>
                    <div className="totalComponents">Total items : {totalItemsCalc()}</div>
                    <div className="totalComponents">Bill Amount : Rs.{calculateBill()}</div>
                    <div className="totalComponents"><button onClick={()=>{
                        navigate('/cart/checkout')
                    }}>Proceed to Checkout</button></div>
                </div>}

            </div>
        </div>

    )
}

export default Cart