import './productpage.css'
import { useParams } from 'react-router-dom'
import { useDispatch, useSelector } from 'react-redux'
import { add2cart } from '../../redux/slices/cart'
import { useEffect, useState } from 'react'

const ProductPage = () => {
    const dispatch = useDispatch()
    const params = useParams()
    const productsInfo = useSelector(reducers => reducers.ProductReducer)
    const [product, setProduct] = useState(null)
    const RegLoginInfo = useSelector(reducers => reducers.RegLoginReducer)

    // const handleAdd2cart = () => {

    // }

    useEffect(() => {
        setProduct(() => productsInfo.productsArr.find(x => x._id === String(params.id)))
    }, [productsInfo.productsArr, params.id])

    return (
        <>
            {product &&

                <div className="productPage">
                    <div className="imagesContainer"><img alt={product.image} src={product.image}></img></div>

                    <div className='featuresNdActions'>
                        <div className="productDetailsContainer">
                            <div className="productDetails"><h2>{product.name}</h2></div>
                            <div className="productDetails"><h3>{product.description}</h3></div>
                            <div>
                                <div className="productDetails">Product Price : Rs.{product.price}</div>
                            </div>
                            <div>
                                <div className="productDetails">Category of Product : {product.category}</div>
                            </div>
                            <div>
                                <div className="productDetails">  {product.quantity > 0 ? 'Currently in stock..' : 'Oops! Currently this product is out of stock..'}</div>
                            </div>
                        </div>



                        {product.quantity > 0 && <button onClick={() => {

                            RegLoginInfo.userAuth.token ?
                                dispatch(add2cart({ userId: RegLoginInfo.loggedinId, product: product })) : alert('Sign in to add items in cart')

                        }}>Add to cart</button>}

                    </div>


                </div>
            }
        </>

    )
}

export default ProductPage