import { useState } from "react"
import { useDispatch, useSelector } from "react-redux"
import { removeFromCart, quantityChanger } from '../../redux/slices/cart'

const CartItem = (props) => {
    const { image, name, price, _id } = props.product
    const dispatch = useDispatch()
    const cartInfo = useSelector(reducers => reducers.CartReducer)
    const RegLoginInfo = useSelector(reducers => reducers.RegLoginReducer)

    const [quantity, setQuantity] = useState('')

    const calcQuantity = () => {
        const { quantityArr } = cartInfo.cartsArr.find(x => x.owner === RegLoginInfo.loggedinId)
        const { quantity } = quantityArr.find(x => x._id === _id)
        return quantity
    }

    return (

        <div className="cartItemBox">
            <div className="cartItemComponets"><img alt={image} src={image}></img></div>
            <div className="cartItemComponets">{name}</div>
            <div className="cartItemComponets cartItemPrice">Rs.{price}</div>

            <div className="cartItemComponets quantityDiv">
                <div>Quantity: {calcQuantity()}</div>
                <div>

                    <input type='text' value={quantity} onChange={(e) => setQuantity(!isNaN(Number(e.target.value))? Number(e.target.value) :alert('Enter numeric value only'))}></input>

                    <button onClick={() => {
                        !isNaN(quantity) && (parseInt(quantity) >= 1 ? dispatch(quantityChanger({ userId: RegLoginInfo.loggedinId, productId: _id, quantity })) : alert('Minimum quantity should be 1'))
                        setQuantity('')
                    }}>Submit</button>
                </div>

            </div>
            <div className="cartItemComponets cartItemDelete"><button onClick={() => {
                dispatch(removeFromCart({ userId: RegLoginInfo.loggedinId, productId: _id }))
            }} >Delete</button></div>

        </div>
    )
}

export default CartItem