import { createSlice } from '@reduxjs/toolkit'

const ProductSliceMaker = createSlice({
    name: 'ProductSlice',
    initialState: {
        productsArr: [],
        product2edit: null
    },
    reducers: {
        setProductsArr: (state, action) => {
            state.productsArr = [...action.payload]
        },
        removeProduct: (state, action) => {
            state.productsArr = [...state.productsArr].filter(product => product._id !== action.payload)
        },
        updateProduct: (state, action) => {
            let product2update = state.productsArr.find(x => x._id === action.payload._id)
            let filtered = state.productsArr.find(x => x._id !== action.payload._id)
            let updatedProduct = { ...product2update, ...action.payload }
            filtered.push(updatedProduct)
            state.productsArr = filtered
        },
        setProduct2edit: (state, action) => {
            state.product2edit = action.payload
        }
    }
})

export const { setProductsArr, updateProduct, removeProduct, setProduct2edit } = ProductSliceMaker.actions
export default ProductSliceMaker.reducer