import { createSlice } from '@reduxjs/toolkit'

const userAuth = JSON.parse(localStorage.getItem('userAuth'));
const { token = null, userType = null } = userAuth || {};


const loginregisterSliceMaker = createSlice({
    name: 'loginregister',
    initialState: {
        login: {
            email: '', password: ''
        },
        register: {
            username: '', email: '', mobile: '', password: '', address: '', secretanswer: ''
        },
        updatePassData: {
            email: '', secretanswer: '', newpassword: ''
        },
        userAuth: {
            userType, token
        },
        verified: { login: null, admin: null },
        userObj: null,
        loggedinId: null
    },
    reducers: {
        setRegisterData: (state, action) => {
            state.register = { ...state.register, ...action.payload }
        },
        setLoginData: (state, action) => {
            state.login = { ...state.login, ...action.payload }
        },
        setUpdatePassData: (state, action) => {
            state.updatePassData = { ...state.updatePassData, ...action.payload }
        },
        setUserAuth: (state, action) => {
            state.userAuth = { ...state.userAuth, ...action.payload }
        },
        setVerified: (state, action) => {
            state.verified = { ...state.verified, ...action.payload }
        },
        setUserObj: (state, action) => {
            state.userObj = { ...action.payload }
        },
        updateUserObj: (state, action) => {
            state.userObj = { ...state.userObj, ...action.payload }
        },
        setLoggedInId: (state, action) => {
            state.loggedinId = action.payload
        }
    }
})

export const { setRegisterData, setLoginData, setUpdatePassData, setUserAuth, setVerified, setUserObj, updateUserObj, setLoggedInId } = loginregisterSliceMaker.actions
export default loginregisterSliceMaker.reducer
