import { Routes, Route } from 'react-router-dom'
import decode from 'jwt-decode'
import { useEffect } from 'react';
import './App.css';
import Homepage from './pages/homepage';
import SignUpForm from './pages/register/signUp'
import LoginForm from './pages/login/login'
import Navbar from './components/Navbar/Navbar'
import ErrorPage from './pages/errorpage.jsx'
import UpdatePassword from './pages/forgotpass/updatepass.jsx'
import Product from './pages/product/product';
import CartPage from './pages/cart/cart.jsx'
import CheckoutPage from './pages/cart/checkoutpage.jsx'
import AdminDash from './Admin/adminDash'
import Addproduct from './Admin/addproduct'
import ProductsList from './Admin/productslist'
import EditProduct from './Admin/editproduct.jsx'
import UserDash from './User/userDash';
import axios from 'axios';
import { useSelector, useDispatch } from 'react-redux';
import { setLoggedInId } from './redux/slices/RegLogin'
import ProtectedRoute from './routes/protectedRoute';
import Editprofile from './components/editprofile/editprofile';
import OrderPage from './pages/order/order.jsx'

import { setCartsArr } from './redux/slices/cart'

import ProductPage from './pages/product/productpage.jsx'

import { setProductsArr } from './redux/slices/product'

import CartProtectedRoute from './routes/cartProtectedRoute'

import CustomersOrdersPage from './pages/order/customerorderspage.jsx'

function App() {
  const dispatch = useDispatch()
  const RegLoginInfo = useSelector(reducers => reducers.RegLoginReducer)
  const cartInfo = useSelector(reducers => reducers.CartReducer)

  useEffect(() => {
    axios.defaults.headers.common['Authorization'] = RegLoginInfo?.userAuth?.token

    axios.get('/products/all').then(res => {
      dispatch(setProductsArr(res.data.products))
    }).catch(err => alert(err.message))

  }, [RegLoginInfo.userAuth, dispatch])

  useEffect(() => {
    const token = JSON.parse(localStorage.getItem('userAuth'))?.token
    if (token !== undefined) {
      const { _id } = token !== undefined && decode(token)

      _id && dispatch(setLoggedInId(_id))
      _id && axios.get(`/carts/${_id}`).then(resp => {
        resp.data.cartdata !== null && dispatch(setCartsArr(resp.data))
      })

    }

  }, [dispatch])

  useEffect(() => {
    const handleBeforeUnload = (event) => {
      event.preventDefault();
      const carts = cartInfo.cartsArr.filter(x => x.owner !== null)
      if (carts.length > 0) {
        navigator.sendBeacon('/carts/add', JSON.stringify(carts));
      }
    };

    window.addEventListener('beforeunload', handleBeforeUnload);

    return () => {
      window.removeEventListener('beforeunload', handleBeforeUnload);
    };
  }, [cartInfo.cartsArr, dispatch]);


  return (
    <div className="App">
      <Navbar />
      <Routes>
        <Route path='/' element={<Homepage />} />
        <Route path='/register' element={<SignUpForm />} />
        <Route path='/login' element={<LoginForm />} />
        <Route path='/updatepassword' element={<UpdatePassword />} />
        <Route path='/products' element={<Product />} />
        <Route path='/productpage/:id' element={<ProductPage />} />

        <Route path='*' element={<ErrorPage />} />

        <Route path='/cart' element={<CartProtectedRoute />} >
          <Route path='' element={<CartPage />} />
          <Route path='checkout' element={<CheckoutPage />} />
        </Route>



        <Route path='/dashboard' element={<ProtectedRoute />}>
          <Route path='user' element={<UserDash />} />
          <Route path='admin' element={<AdminDash />} />
          <Route path='editprofile' element={<Editprofile />} />
          <Route path='admin/addproduct' element={<Addproduct />} />
          <Route path='admin/products' element={<ProductsList />} />
          <Route path='admin/editproduct' element={<EditProduct />} />
          <Route path='user/orders' element={<OrderPage/>} />
          <Route path='admin/personalorders' element={<OrderPage/>} />
          <Route path='admin/orders' element={<CustomersOrdersPage/>} />
        </Route>

      </Routes>
    </div>
  );
}

export default App;
