import { useSelector } from "react-redux"
import { useNavigate } from "react-router-dom"
import Adminmenu from '../components/adminmenu'

const AdminDash = () => {
  const navigate = useNavigate()
  const RegLoginInfo = useSelector(reducers => reducers.RegLoginReducer)
  return (
    <div>
      {RegLoginInfo.verified.admin === true &&
        <>
          <Adminmenu />
          <div>
            {RegLoginInfo.userObj && Object.entries(RegLoginInfo.userObj).map(x =>
              <div>
                {String(x[0]) !== '_id' && `${x[0]} ---> ${Number(x[1]) === 0 ? 'Normal User' : Number(x[1]) === 1 ? 'Admin' : x[1]}`}
              </div>)}
          </div>
        </>
      }
      {RegLoginInfo.verified.admin === false &&
        <>
          <div>Unathorized access! You are not signed in as Admin.</div>
          <button onClick={() => navigate('/dashboard/user')}>Visit user dashboard</button>
        </>
      }
    </div>
  )
}

export default AdminDash
