import { NavLink, useNavigate } from 'react-router-dom'
import './login.css'
import axios from "axios";
import { setLoginData, setUserAuth, setUserObj, setLoggedInId } from '../../redux/slices/RegLogin'
import { setCartsArr } from '../../redux/slices/cart'
import { useDispatch, useSelector } from 'react-redux';
import decode from 'jwt-decode'

const LoginForm = () => {
    const dispatch = useDispatch()
    const RegLoginInfo = useSelector(reducers => reducers.RegLoginReducer)
    const loginData = RegLoginInfo.login
    // const userAuthData = RegLoginInfo.userAuth

    const navigate = useNavigate()

    function handleChange(e) {
        const { id, value } = e.target
        dispatch(setLoginData({ [id]: value }))
    }

    async function handleLogin(e) {
        e.preventDefault()
        const valuesArr = Object.values(loginData)
        let formFields = []
        for (let key in valuesArr) {
            (valuesArr[key] === '') && formFields.push(Object.keys(loginData)[key])
        }
        if (formFields.length === 0) {
            try {
                const response = await axios.post('/login', loginData)
                const { _id } = decode(response.data.token)
                axios.get(`/carts/${_id}`).then(resp => {
                    resp.data.cartdata !== null && dispatch(setCartsArr(resp.data))
                })
                const { userType, token, userObj } = response.data
                axios.defaults.headers.common['Authorization'] = token
                dispatch(setUserAuth({ userType, token }))
                dispatch(setUserObj(userObj))
                dispatch(setLoggedInId(_id))
                localStorage.setItem('userAuth', JSON.stringify({ userType, token }))
                String(response.status) === '200' ? navigate('/') : alert(response.data.message)
            } catch (error) {//this catch block also catches client side data errors
                console.log(error.response.data)
                alert(error.response.data.message)
            }
            dispatch(setLoginData({ email: '', password: '' }))

        } else if (formFields.length < valuesArr.length) {
            alert(`${formFields.join(',')} are empty`)
        } else {
            alert('All field must be filled to Login')
        }
    }
    return (
        <div className='loginPage'>
            <div className='formContainer' >
                <form className='loginForm' onSubmit={e => handleLogin(e)}>

                    <div className='formsComponent'>
                        <label htmlFor="email">Email:</label>
                        <input type="email" id="email" value={loginData.email} onChange={(e) => handleChange(e)} required />
                    </div>

                    <div className='formsComponent'>
                        <label htmlFor="password">Enter Password:</label>
                        <input type="password" id="password" value={loginData.password} onChange={(e) => handleChange(e)} required />
                    </div>

                    <div className='formsComponent'>
                        <input type="submit" value="Login" />
                    </div>
                    <NavLink to='/updatepassword'>Forgot Password?</NavLink>

                </form>
                <div className='go2login'>
                    <button onClick={() => navigate('/register')}>New User?Register yourself..</button>
                </div>
            </div>

        </div>
    )
}

export default LoginForm