import { useNavigate } from 'react-router-dom'
import axios from "axios";
import { useState } from 'react';

const Addproduct = () => {
    const [product, setProduct] = useState({ name: '', price: '', quantity: '', description: '', category: '', image: null })

    const navigate = useNavigate()
    const convert2base64 = (imgFile) => {
        return new Promise((resolve, reject) => {
            const fileReader = new FileReader()
            fileReader.readAsDataURL(imgFile)
            fileReader.onload = () => resolve(fileReader.result)
            fileReader.onerror = (err) => reject(err)
        })
    }
    const handleChange = async (e) => {
        if (e.target.files) {
            const imgfile = e.target.files[0]
            const fileSize = e.target.files[0].size;
            const sizeLimit = 5 * 1024 * 1024;//5MB    
            if (fileSize <= sizeLimit) {
                const imgInBase64 = await convert2base64(imgfile)
                const { id } = e.target
                if (id === 'image') {
                    setProduct(() => ({ ...product, [id]: imgInBase64 }))
                }
            } else {
                alert('Size limit exceeded');
                e.target.value = ''; // Clear the selected file
            }
        }else{
            const { id,value } = e.target
            setProduct(() => ({ ...product, [id]: value }))
        }
    };

    function handleProductAddition(e) {
        e.preventDefault()
        const valuesArr = Object.values(product)
        let formFields = []
        for (let key in valuesArr) {
            (valuesArr[key] === '') && formFields.push(Object.keys(product)[key])
        }
        if (formFields.length === 0) {
            let formData = new FormData()
            let entries = Object.entries(product)
            for (let key in entries) {
                formData.append(`${entries[key][0]}`, entries[key][1])
            }

            // Iterate over FormData entries and log them individually
            // for (let pair of formData.entries()) {
            //     console.log('pairs: ',pair[0], pair[1]);
            // }

            axios.post('/products/add', formData, {
                headers: {
                    'Content-Type': 'application/json'
                }
            }).then(response => {
                alert(response.data.message)
                navigate('/dashboard/admin')
            }).catch(err => {
                alert('Error :', err.message)
            })
            setProduct({ name: '', price: '', quantity: '', description: '', category: '', image: null })


        } else if (formFields.length < valuesArr.length) {
            alert(`${formFields.join(',')} are empty`)
        } else {
            alert('All field must be filled to Register')
        }
    }
    return (
        <div>
            <form className='signupForm' onSubmit={e => handleProductAddition(e)} encType='multipart/form-data'>

                <div className='formsComponent'>
                    <label htmlFor="name">Name:</label>
                    <input type="text" id="name" value={product.name} onChange={(e) => handleChange(e)} required />
                </div>

                <div className='formsComponent'>
                    <label htmlFor="price">Price:</label>
                    <input type="number" id="price" value={product.price} onChange={(e) => handleChange(e)} required />
                </div>

                <div className='formsComponent'>
                    <label htmlFor="category">Category:</label>
                    <input type="text" id="category" value={product.category} onChange={(e) => handleChange(e)} required />
                </div>

                <div className='formsComponent'>
                    <label htmlFor="description">Description:</label>
                    <input type="text" id="description" value={product.description} onChange={(e) => handleChange(e)} required />
                </div>

                <div className='formsComponent'>
                    <label htmlFor="quantity">Quantity:</label>
                    <input type="number" id="quantity" value={product.quantity} onChange={(e) => handleChange(e)} required />
                </div>

                <div className='formsComponent'>
                    <label htmlFor="image">Upload Image:</label>
                    <input type='file' id="image" onChange={(e) => handleChange(e)} required />
                </div>


                <div className='formsComponent'>
                    <input type="submit" value="Add Product" />
                </div>

            </form>
            <div className='go2login'>
                <button onClick={() => navigate('/dashboard/admin/products')}>See added products</button>
            </div>
        </div>
    )
}

export default Addproduct