import { NavLink } from "react-router-dom"
import './menu.css'

const Usermenu = () => {
    return (
        <div className="menu">
            <div><NavLink className='navlinks' to='/dashboard/user/orders'>Your Orders</NavLink></div>
            <div><NavLink className='navlinks' to='/dashboard/editprofile'>Edit Profile</NavLink></div>
        </div>
    )
}

export default Usermenu