import { useSelector, useDispatch } from "react-redux"
import { useNavigate } from "react-router-dom"
import { setUserObj, updateUserObj } from '../../redux/slices/RegLogin'
import axios from "axios"
const Editprofile = () => {
    const navigate = useNavigate()
    const dispatch = useDispatch()
    const RegLoginInfo = useSelector(reducers => reducers.RegLoginReducer)
    const userObjBackup = RegLoginInfo.userObj
    const handleChange = (e) => {
        const { id, value } = e.target
        dispatch(updateUserObj({ [id]: value }))
    }
    const handleUpdate = async (e) => {
        e.preventDefault()
        try {
            const response = await axios.post('/updateuser', { updateData: RegLoginInfo.userObj,email:userObjBackup.email })
            dispatch(setUserObj({...response.data.updatedUser}))
            alert('Successfully updated')
            userObjBackup.userType === 1 ? navigate('/dashboard/admin') : navigate('/dashboard/user')
        } catch (error) {
            alert(error.message)
            dispatch(updateUserObj({ ...userObjBackup }))
        }
    }
    return (
        <div className='formContainer' >
            <form className='signupForm' onSubmit={e => handleUpdate(e)}>

                <div className='formsComponent'>
                    <label htmlFor="username">Name:</label>
                    <input type="text" id="username" value={RegLoginInfo?.userObj?.username} onChange={(e) => handleChange(e)} required />
                </div>

                <div className='formsComponent'>
                    <label htmlFor="email">Email:</label>
                    <input type="email" id="email" value={RegLoginInfo?.userObj?.email} onChange={(e) => handleChange(e)} required disabled />
                </div>

                <div className='formsComponent'>
                    <label htmlFor="mobile">Mobile:</label>
                    <input type="number" id="mobile" value={RegLoginInfo?.userObj?.mobile} onChange={(e) => handleChange(e)} required />
                </div>

                <div className='formsComponent'>
                    <label htmlFor="address">Address:</label>
                    <input type="text" id="address" value={RegLoginInfo?.userObj?.address} onChange={(e) => handleChange(e)} required />
                </div>

                <div className='formsComponent'>
                    <input type="submit" value="Update" />
                </div>

            </form>
        </div>
    )
}

export default Editprofile

